.container{
  display: flex;
  background: linear-gradient( rgb(0, 18, 52), rgb(0, 0, 0));
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 80%;margin: auto;
}

h1{
  text-align: center;
  color: #ffffff;
  background: linear-gradient(135deg, #0d1120 0%, #3a4b8a 43%, #0d1120 100%);padding: 10px;
}

.card {
  display: flex;
  flex-direction: column;
  margin: 10px;
  width: 270px;
  height: max(350px,fit-content);
  padding: .8em;
  background: linear-gradient(#fff2, transparent);
  backdrop-filter: blur(10px);
  position: relative;
  border-radius: 20px;
  border: 1px solid white;
  box-shadow: 0 0px 3px 2px rgba(154, 153, 153, 0.162);
 }
 
img {
  height: 230px;
  width: 100%;
  background-position: center;
  background-size:contain;
  border-radius: 15px;
 }
 
 .card-info {
  padding-top: 10%;
  color:#dceeef ;
 }

 .card-footer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
 
  color: #a0f6f8;
  padding: 10px ;
  box-sizing: border-box;
  border-top: 2px solid #FDDE55;
 }
 
 
 .text-title {
  font-weight: 300;
  font-size: 1.2em;
  line-height: 1.5;
 }
 
 .text-body {
  font-size: .9em;
  padding-bottom: 10px;
 }
